/* Font Weights */
/* Font Families */
/* Headlines */
h1 {
  font-family: "Tiempos-Medium", "Georgia", Times, Times New Roman, serif;
  font-size: 40px;
  line-height: 52px; }
  @media only screen and (min-width: 768px) {
    h1 {
      line-height: 64px; } }

h3 {
  padding-bottom: 10px;
  padding-top: 10px; }

h4 {
  font-family: "Corbel-Bold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 20px;
  letter-spacing: -.18px;
  line-height: 26px;
  margin-bottom: 20px;
  margin-top: 20px; }
  @media only screen and (min-width: 768px) {
    h4 {
      font-size: 22px;
      letter-spacing: -.2px;
      line-height: 26px; } }

h5 {
  font-family: "Tiempos-Medium", "Georgia", Times, Times New Roman, serif;
  font-size: 18px;
  line-height: 24px; }

/* Mixins for Heading */
/* Heading 1 */
h1 {
  color: #000; }

/* Heading 3 */
h3 {
  color: #000; }

/* Heading 4 */
h4 {
  color: #000; }

/* Heading 5 */
h5 {
