/* Heading Margins */
h2 {
  color: #1c5cef;
  font-size: 30px;
  margin-bottom: 35px;
  text-align: center; }

/******************
     BODY TEXT
*******************/
.body--default-bold, .text-bold, .body--default, body,
p,
ul,
ol {
  color: #000;
  font-family: "Corbel", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 19px;
  line-height: 26px; }

.body--default-bold, .text-bold {
  font-family: "Corbel-Bold", Helvetica Neue, Helvetica, Arial, sans-serif; }

/* Classes for Body Text */
em,
i {
  font-style: italic; }

strong {
  font-weight: 700; }

p:not(:last-child) {
  margin-bottom: 30px;
  text-align: center; }

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative; }

body {
  background-color: #f2f2f2;
  color: #000;
  margin: 0; }

img {
  max-width: 100%; }

a,
button,
input {
  outline: none; }

a {
  color: inherit;
  cursor: pointer; }

.row {
  margin-left: -15px;
  margin-right: -15px;
  width: calc(100% + 30px); }

.container {
  margin: 0 auto;
  max-width: 1200px;
  position: relative; }
  .container::after {
    clear: both;
    content: "";
    display: block; }

section {
  margin-bottom: 80px;
  position: relative;
  width: 100%; }
  section::after {
    clear: both;
    content: "";
    display: block; }
  @media only screen and (min-width: 768px) {
    section {
      margin-bottom: 50px; } }
  @media only screen and (min-width: 992px) {
    section {
      margin-bottom: 70px; } }

/********************
     Transitions
*********************/
a {
  -ms-transition: all 0.5s cubic-bezier(0, 0, 0.2, 1);
  -webkit-transition: all 0.5s cubic-bezier(0, 0, 0.2, 1);
  transition: all 0.5s cubic-bezier(0, 0, 0.2, 1); }

/********************
      generator
********************/
.choose-file {
  margin-top: 50px;
  text-align: center; }
  .choose-file h3 {
    font-size: 26px; }
  .choose-file input {
    background-color: #007bff; }
  .choose-file .custom-file {
    position: relative; }
    .choose-file .custom-file .custom-file-control {
      background-color: #fff;
      border: 1px solid rgba(0, 0, 0, 0.15);
      border-radius: 4px;
      left: 0;
      line-height: 1.5;
      padding: 8px 16px;
      position: absolute;
      right: 0;
      top: 0; }
      .choose-file .custom-file .custom-file-control:before {
        background-color: #007bff;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 0 .4px .4px 0;
        content: "Browse";
        padding: 8px 16px;
        position: absolute;
        right: -40px;
        top: -1px; }
      .choose-file .custom-file .custom-file-control:after {
        content: "Choose file..."; }

.data-table {
  margin-top: 50px; }
  .data-table table {
    width: 100%; }
    .data-table table tr:nth-child(even) {
      background-color: rgba(0, 0, 0, 0.05); }
    .data-table table tr td {
      border-top: 1px solid #dee2e6;
      padding: 10px; }
      .data-table table tr td:first-child {
        background-color: #8099b566;
        text-align: center; }
      .data-table table tr td button {
        background-color: #007bff;
        border: 1px solid #007bff;
        border-radius: 4px;
        color: #fff;
        font-size: 16px;
        line-height: 1.5;
        padding: 5px 25px; }

/********************
      reader
********************/
.reader .QR-video {
  margin-top: 50px; }
  @media only screen and (min-width: 992px) {
    .reader .QR-video {
      display: flex;
      justify-content: space-between; } }
  .reader .QR-video .left,
  .reader .QR-video .right {
    border: 1px solid #007bff;
    border-radius: 15px;
    margin-bottom: 20px;
    padding: 20px;
    width: calc(100% - 30px);
    float: left;
    margin-left: 15px; }
    @media only screen and (min-width: 768px) {
      .reader .QR-video .left,
      .reader .QR-video .right {
        width: calc(50% - 22.5px);
        float: left;
        margin-left: 15px; } }
  .reader .QR-video .left h4,
  .reader .QR-video .left .button_container {
    text-align: center; }
  .reader .QR-video .left .button_container .button {
    background-color: #007bff;
    border: 1px solid #007bff;
    border-radius: 4px;
    color: #fff;
    font-size: 16px;
    line-height: 1.5;
    margin-left: 10px;
    padding: 5px 25px; }
  .reader .QR-video .right #sourceSelectPanel {
    margin-bottom: 20px; }
    .reader .QR-video .right #sourceSelectPanel select {
      background: transparent;
      border: 1px solid #626971;
      border-radius: 4px;
      font-size: 16px;
      height: 34px;
      line-height: 1;
      padding: 5px;
      width: 268px; }
